<template>
    <div class="main"  v-loading="loading">
        <lang></lang>
        <!--  Modal edit question -->
        <div>
            <el-dialog  :visible.sync="modalEditQuestion" :title="lang['edit-question']" center  top="5vh">
                <form id="form-edit-question" @submit.prevent="editQuestion()">
                    <div class="form-row">
                        <div class="form-group col-xl-6 col-md-6">
                            <!-- Question id -->
                            <input type="number" class="hide" v-model="questionId" name="questionId">
                            <!-- Question type -->
                            <h4>{{lang["question-type"]}}</h4>
                            <br>
                            <el-radio-group @change="updateAnswersList()" v-model="type">
                                <el-radio name="type" :label="1">{{lang["descriptive"]}}</el-radio>
                                <el-radio name="type" :label="2">{{lang["multiple-choice"]}}</el-radio>
                                <el-radio name="type" :label="3">{{lang["free"]}}</el-radio>
                            </el-radio-group>
                        </div>
                        <div class="form-group col-xl-6 col-md-6">
                            <!-- Question weight -->
                            <h4>{{lang["question-weight"]}}</h4>
                            <br>
                            <div class="block">
                                <input type="number" class="hide" name="weight" v-model="weight">
                                <el-rate  v-model="weight"></el-rate>
                            </div>
                        </div>
                    </div>
                    <hr>
                    <div class="form-row">
                        <div class="form-group col-xl-6 col-md-6">
                            <input class="hide" type="text" name="examId" :value="examId">
                            <!-- Question -->
                            <h4>{{lang["write-your-question"]}} *</h4>
                            <el-input rows="4" type="textarea" v-model="question" required name="question"></el-input>
                            <br>
                            <div v-if="type == 2">
                                <answer-list :question-id="questionId"></answer-list>
                                <br>
                                <el-button type="primary"  @click.prevent="createNewAnswerEvent()" size="medium">{{lang["add-new-answer"]}}</el-button>
                            </div>
                        </div>
                        <div class="form-group col-xl-6 col-md-6">
                            <!-- Question image -->
                            <h4>{{lang["question-image"]}}</h4>
                            <div class="drop-area" style="height:100px !important;"> 
                                <input :value="questionImageName" name="questionImageName" type="text">
                                <input 
                                    class="upload"  
                                    @change.prevent="render($event)"  
                                    type="file"
                                    acceptable=".png,.jpg,.jpeg"
                                >
                                <div v-if="questionImageName == null || questionImageName == '' " class="drop-message">
                                    <span class="file-icon" :class="icon"></span>
                                    <p>{{message}}</p>
                                </div>
                                <div class="drop-preview on" style="text-align:center;">
                                    <div class="drop-img">
                                        <img v-if="questionImageName != null && questionImageName != ''" id="previewQuestionImage" class="preview" :src="previewImg">
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="form-row" v-if="type == 2">
                        <div class="form-group col-xl-12 col-md-12">
                            <!-- Automatic feedback -->
                            <h4>{{lang["automatic-feedback"]}}</h4>
                            <el-input rows="5" type="textarea" v-model="automaticFeedback" name="feedback" class="textarea-question"></el-input>
                        </div>
                    </div>
                    <br>
                    <div class="form-row">
                        <div class="form-group col-xl-6 col-md-6">
                            <el-button class="btn-sabiorealm" v-loading="loading"  native-type="submit"  type="primary"  size="medium">{{lang["save-button"]}}</el-button>
                        </div>
                    </div>
                </form>  
            </el-dialog>
        </div>
        <!-- End  modal new module -->
    </div>
</template>

<script>
import Vue from 'vue'
import axios from 'axios'
import VueAxios from 'vue-axios'
import VueTheMask from 'vue-the-mask'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import Lang from '@/components/helper/HelperLang.vue'
import lang from 'element-ui/lib/locale/lang/en'
import locale from 'element-ui/lib/locale'
import AnswerList from '@/components/answers/AnswerList'
import domains from '@/mixins/domains'
import alerts from '@/mixins/alerts'
import {eventLang} from '@/components/helper/HelperLang'
import {eventBus} from '@/pages/newcourse/App'
import {eventProgress} from '@/components/helper/HelperProgress'

locale.use(lang)
Vue.use(VueTheMask)
Vue.use(VueTheMask)
Vue.use(VueAxios, axios)
Vue.use(ElementUI)


export default {
    mixins: [domains,alerts],
    components: {
        Lang, 
        AnswerList
    },
    data: () => {
        return {
            lang: {},
            modalEditQuestion: false,
            loading: false,
            questionId: '',
            question: '',
            type: 1,
            weight: '',
            automaticFeedback: '',
            questionImageName : '',
            previewImg: '',
            examId: '',
            icon: 'fas fa-cloud-upload-alt',
            message:"Upload a file",
        }
    },
    mounted(){
        eventLang.$on('lang', function(response){  
            this.lang = response;
        }.bind(this));

        eventBus.$on('new-open-modal-edit-question', function(response){
            this.questionId = response["questionId"];
            this.question = response["question"];
            this.type =  parseInt(response["type"]);
            this.weight = parseInt(response["weight"]);
            this.automaticFeedback = response["feedback"];
            this.questionImageName = response["image"];
            this.previewImg = ''+this.getCurrentDomainName()+'assets/uploads/question/' + response["image"]
            this.examId = response["examId"];
            this.modalEditQuestion = true;
        }.bind(this));
    },
    methods: {
        createNewAnswerEvent: function(){
            eventBus.$emit("open-answer-modal", this.questionId);
        },
        updateAnswersList: function(){
            if(this.type == 2){
                eventBus.$emit("new-answer");
            }
        },
        editQuestion: function(){
            var form = document.getElementById('form-edit-question')
            var formData = new FormData(form)
            var urlToBeUsedInTheRequest = this.getUrlToMakeRequest("question", "edit");
            axios.post(urlToBeUsedInTheRequest, formData).then(() => {
                this.successMessage();
                this.actionsToBePerformedAfterEdit();
            }, 
                function(){
                    this.errorMessage();
                }.bind(this)
            );
        },
        render: function(event){
            this.upload(event);
            this.message = '';
            this.icon = '';
            var input = event.target; 
            var fullName = input.value;  
            var fileName = fullName.split(/(\\|\/)/g).pop();
            var fileExtension = fullName.split('.').pop();
            this.realName = fileName;
            if(fileExtension == 'png' || fileExtension == 'jpg' || fileExtension == 'jpeg' ){
            var reader = new FileReader();
                reader.onload = function (e) {
                var div = input.parentElement; 
                var img = div.getElementsByTagName('img')[0];
                img.src = e.target.result;
            };  
            reader.readAsDataURL(input.files[0]);
            }else{
            this.message = fileName;
            this.icon = "far fa-thumbs-up text-default";
            }
        },
        upload: function(event){
            eventProgress.$emit("new-progress");
            const config = {
                onUploadProgress: function(progressEvent) {
                var percentCompleted = Math.round((progressEvent.loaded * 100) / progressEvent.total)
                eventProgress.$emit("new-percent", percentCompleted);
                }
            }
            var formData = new FormData()
            formData.append('file', event.target.files[0])
            formData.append('type', 'question-image')
            var urlToBeUsedInTheRequest = this.getUrlToMakeRequest("upload", "upload_file");
            axios.post(urlToBeUsedInTheRequest,formData,config).then((response) => {
                this.questionImageName = response.data;
                event.target.value = null
                eventProgress.$emit("finish-progress");
            }, 
                /* Error callback */
                function(){
                    this.errorMessage();
                }.bind(this)
            );
        },
        actionsToBePerformedAfterEdit(){    
            this.modalEditQuestion = false; 
            eventBus.$emit("new-question");
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.textarea-question{
    height:100px !important; 
}

.module-name{
    max-width: 30ch;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.box-no-results{
    background-color: #f5f5f7;
}

.text-no-results{
    margin-top:10%;
}

#accordion > .card{
    margin-bottom:2%;
}

.btn-link{
    margin: 0px !important;
    padding: 0px !important;
    font-size:13px;
    font-family: 'Poppins', sans-serif;
    font-weight: 600;
}


.btn.link:hover{
    text-decoration: none !important;
}

.card-header{
    padding:10px !important;
}

.btn-eadtools-rounded{
    border-radius: 50%;
    cursor: pointer;
    display: inline-block;
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.26);
    position: relative;
    height: 40px;
    width: 40px;
    background-color: #D696EF;
    z-index: 2;
    border:none;
    color:white;
    margin: 0 0.2rem;
}


.sortable-drag{
    opacity: 0;
}

.flip-list-move{
    transition: transform 0.5s;
}

.ghost{
    border-left:6px solid rgb(0, 183, 255);
    border-radius:5px;
    opacity: .5;
}


.card{
    margin-bottom: 15px;
}

.handle{
    cursor:move;
}


li{
    list-style-type: none;
}

.lesson{
    text-align:center;
    text-transform: uppercase;
    font-size:10px;
}

.lesson span{
    font-family: 'Poppins', sans-serif;
    margin-top:3em;
    font-size:1em;
}

</style>
